import authContext from "./authContext";
import { React, useState, useEffect } from "react";
import Swal from "sweetalert2";
import { useCookies } from 'react-cookie';
import Cookies from 'universal-cookie';
const cookies = new Cookies();
require('dotenv').config()


function importAll(r) {
  return r.keys().map(r);
}
const welcomeImg = importAll(
  require.context(
    "./img/random/swal/welcome",
    false,
    /\.(png|webm|jfif|webp|gif|jpe?g|svg)$/
  )
);
const welcomeText = [
  "You're very welcome",
  "Please accept my deepest thanks",
  "I’m glad that you came to meet me",
  "It is my pleasure that you are here",
];

const byeImg = importAll(
  require.context(
    "./img/random/swal/bye",
    false,
    /\.(png|webm|jfif|webp|gif|jpe?g|svg)$/
  )
);
const byeText = [
  "Have a nice day",
  "See you next time",
  "Keep in touch",
  "I look forward to our next work",
];

const thanksImg = importAll(
  require.context(
    "./img/random/swal/thanks",
    false,
    /\.(png|webm|jfif|webp|gif|jpe?g|svg)$/
  )
);
const thanksText = [
  "Please accept my deepest thanks",
  "Thanks for being our teammate",
  "So glad you’re on our team",
];

const loadingImg = importAll(
  require.context(
    "./img/random/swal/loading",
    false,
    /\.(png|webm|jfif|webp|gif|jpe?g|svg)$/
  )
);

const noInternet = importAll(
  require.context(
    "./img/random/swal/noInternet",
    false,
    /\.(png|webm|jfif|webp|gif|jpe?g|svg)$/
  )
);


// eslint-disable-next-line no-extend-native
Array.prototype.chooseOne = function () {
  return this[Math.floor(Math.random() * this.length)];
};

export default function AuthState(props) {
  const Toast = Swal.mixin({
    toast: true,
    position: "top-end",
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener("mouseenter", Swal.stopTimer);
      toast.addEventListener("mouseleave", Swal.resumeTimer);
    },
  });

  const swalWait = (waitText) => {
    Swal.fire({
      title: "Please Wait !",
      imageUrl: loadingImg.chooseOne().default,
      imageWidth: 400,
      imageHeight: 200,
      imageAlt: "welcome image error",
      html: `${waitText}.....`,
      backdrop: true,
      showConfirmButton: false,
      allowOutsideClick: () => {
        const popup = Swal.getPopup();
        popup.classList.remove("swal2-show");
        setTimeout(() => {
          popup.classList.add("animate__animated", "animate__headShake");
        });
        setTimeout(() => {
          popup.classList.remove("animate__animated", "animate__headShake");
        }, 500);
        return false;
      },
    });
  };

  const validateImage = (fileName) => {
    const allowedExtensions =
      /(\.jpg|\.jpeg|\.png|\.webp|\.svg|\.gif|\.jfif)$/i;
    if (!allowedExtensions.exec(fileName)) {
      Swal.fire(
        "File type error!!",
        "Please upload file having extensions .jpeg/.jpg/.png/.webp/.svg/.gif/.jfif only.",
        "error"
      );
    } else {
      return true;
    }
  };

  const uploadImage = async (e) => {
    const file = e.target.files[0];
    const base64 = await convertBase64(file);
    return base64;
  };

  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = () => {
        resolve(fileReader.result);
      };

      fileReader.onerror = (error) => {
        reject(error);
        Toast.fire({
          icon: "error",
          title: `Image file error!!!`,
        });
      };
    });
  };

  const [newUserImageUrl, setNewUserImageUrl] = useState(
    "data:image/jpeg;base64,"
  );
  
  // eslint-disable-next-line
  const [gcecookies, setGcecookie, removeGcecookie] = useCookies(['cookie-name']);
  //Get current user data if logged in
  const getuser = async () => {
    //Api call
    if (cookies.get("GCE_KR_TOKEN")) {
      const response = await fetch(
        `${process.env.REACT_APP_API}/api/auth/user`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            "auth-token": cookies.get("GCE_KR_TOKEN"),
          },
        }
      ).catch((err) => {
        return { status: false };
      });
      if (response.status === 200) {
        const json = await response.json();
        return json;
      }
    } else {
      return { status: false };
    }
  };

  //Signup
  const signup = async () => {
    const { value: accept } = await Swal.fire({
      title: "Welcome to GCE-KR SignUp",
      inputLabel:
        "Please accept out terms and conditions to fill the SignUp form.",
      input: "checkbox",
      inputValue: 0,
      inputPlaceholder: "I agree with the terms and conditions",
      confirmButtonText: 'Continue <i class="fa fa-arrow-right"></i>',
      inputValidator: (result) => {
        return !result && "You need to agree with T&C";
      },
    });

    if (accept) {
      const steps = ["1", "2", "3", "4", "5"];
      const swalQueue = Swal.mixin({
        progressSteps: steps,
        confirmButtonText: "Next >",
        reverseButtons: true,
        backdrop: true,
        allowOutsideClick: () => {
          const popup = Swal.getPopup();
          popup.classList.remove("swal2-show");
          setTimeout(() => {
            popup.classList.add("animate__animated", "animate__headShake");
          });
          setTimeout(() => {
            popup.classList.remove("animate__animated", "animate__headShake");
          }, 500);
          return false;
        },
      });

      const { value: newUserName } = await swalQueue.fire({
        title: "Name",
        input: "text",
        inputLabel: "Enter your name",
        currentProgressStep: 0,
        inputAttributes: {
          required: true,
          minlength: 3,
        },
        validationMessage: "Minimum 3 characters required !",
      });

      const { value: newUserEmail } = await swalQueue.fire({
        title: "Email",
        input: "email",
        inputLabel: "Enter your email",
        inputPlaceholder: "abc@xyz.com",
        currentProgressStep: 1,
        inputAttributes: {
          required: true,
        },
        validationMessage: "Please enter a valid email id !",
      });

      const { value: newUserPassword } = await swalQueue.fire({
        title: "Enter your password",
        input: "password",
        inputLabel: "Password",
        inputPlaceholder: "Enter your password",
        currentProgressStep: 2,
        inputAttributes: {
          maxlength: 20,
          minlength: 5,
          autocapitalize: "off",
          autocorrect: "off",
          required: true,
        },
        validationMessage: "Minimum 5 characters required !",
      });

      await swalQueue.fire({
        title: "Profile image",
        html: `<span>Not required</span>
              <input 
                style="width: 80%;
                  height: 59px;
                  padding-top: 10px;" 
                type="file" 
                id="file" 
                class="swal2-input"
              />`,
        currentProgressStep: 3,
        showDenyButton: true,
        denyButtonText: `Skip`,
        preConfirm: () => {
          if (document.querySelector("#file").files[0]) {
            if (
              validateImage(
                Swal.getPopup().querySelector("#file").files[0].name
              )
            ) {
              const file = Swal.getPopup().querySelector("#file");
              var reader = new FileReader();
              reader.readAsDataURL(file.files[0]);
              reader.onload = function () {
                setNewUserImageUrl(reader.result);
              };
            }
          }
        },
      });

      const { value: secretCode } = await swalQueue.fire({
        title: "Secret code",
        text: "You can't SignUp without a valid Secret Code",
        input: "text",
        inputLabel: "Enter a valid Secret Code",
        inputPlaceholder: "Only admin knows",
        currentProgressStep: 4,
        backdrop: true,
        allowOutsideClick: () => {
          const popup = Swal.getPopup();
          popup.classList.remove("swal2-show");
          setTimeout(() => {
            popup.classList.add("animate__animated", "animate__headShake");
          });
          setTimeout(() => {
            popup.classList.remove("animate__animated", "animate__headShake");
          }, 500);
          return false;
        },
        inputAttributes: {
          required: true,
          minlength: 6,
        },
        validationMessage: "Minimum 5 characters required !",
      });

      swalWait("Signing up");
      //Api call
      const response = await fetch(
        `${process.env.REACT_APP_API}/api/auth/signup`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: `{
          "name": "${newUserName}",
          "email": "${newUserEmail}",
          "password": "${newUserPassword}",
          "image": "${newUserImageUrl}",
          "secret_code": "${secretCode}"
        }`,
        }
      ).catch(() => {
        Swal.close();
        Toast.fire({
          icon: "error",
          title: `Server error!!!`,
        });
      });
      if (response) {
        Swal.close();
        const json = await response.json();
        if (response.status === 200) {
          Toast.fire({
            icon: "success",
            title: `Thanks ${newUserName}, now you can log in`,
          });
          Swal.fire({
            title: `Thanks ${newUserName}!`,
            text: `${thanksText.chooseOne()} 😁`,
            imageUrl: thanksImg.chooseOne().default,
            imageWidth: 400,
            imageHeight: 200,
            imageAlt: "Bye image error",
          });
        } else if (response.status === 401) {
          Toast.fire({
            icon: "error",
            title: json.msg,
          });
        } else {
          json.msg.forEach((element) => {
            Toast.fire({
              icon: "error",
              title: element.msg,
            });
          });
        }
      }
    }
  };

  //Login
  const login = async () => {
    const { value: loginId } = await Swal.fire({
      title: "Welcome to GCE-KR login",
      input: "email",
      inputPlaceholder: "example@gce.kr",
      confirmButtonText: "Next >",
      inputLabel: "Enter email id",
      inputAttributes: {
        required: true,
      },
      validationMessage: "Please enter a valid email id !",
    });
    if (loginId) {
      const { value: loginPass } = await Swal.fire({
        title: "Enter your password",
        input: "password",
        inputLabel: "Password",
        confirmButtonText: "Login",
        inputPlaceholder: "Enter your password",
        inputAttributes: {
          maxlength: 20,
          minlength: 5,
          autocapitalize: "off",
          autocorrect: "off",
          required: true,
        },
        validationMessage: "Minimum 5 characters required !",
      });
      if (loginPass) {
        swalWait("Logging in");
        //Api call
        const response = await fetch(
          `${process.env.REACT_APP_API}/api/auth/login`,
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: `{
              "email": "${loginId}",
              "password": "${loginPass}"
            }`,
          }
        ).catch(async () => {
          Swal.close();
          Toast.fire({
            icon: "error",
            title: `Server error!!!`,
          });
        });
        if (response) {
          const json = await response.json();
          if (response.status === 200) {
            setGcecookie('GCE_KR_TOKEN', json.authToken, { path: '/', sameSite: 'strict', secure: true, }); //access it with "cookies.get("GCE_KR_TOKEN")"
            setCurrentUser(await getuser());
            Swal.close();
            Swal.fire({
              title: `Hello ${json.name}!`,
              text: `${welcomeText.chooseOne()} 😄`,
              imageUrl: welcomeImg.chooseOne().default,
              imageWidth: 400,
              imageHeight: 200,
              imageAlt: "welcome image error",
            });
          } else if (response.status === 401) {
            Swal.close();
            Toast.fire({
              icon: "error",
              title: json.msg,
            });
          } else {
            json.msg.forEach((element) => {
              Swal.close();
              Toast.fire({
                icon: "error",
                title: element.msg,
              });
            });
          }
        }
      }
    }
  };

  //Logout
  const logout = async () => {
    Swal.fire({
      title: `Bye ${currentUser.user.name}!`,
      text: `${byeText.chooseOne()} 😁`,
      imageUrl: byeImg.chooseOne().default,
      imageWidth: 400,
      imageHeight: 200,
      imageAlt: "Bye image error",
    });
    removeGcecookie("GCE_KR_TOKEN")
    setCurrentUser({ status: false });
  };

  const [currentUser, setCurrentUser] = useState({ status: false });
  useEffect(() => {
    setCurrentUser(getuser());
    // eslint-disable-next-line
  }, []);
  return (
    <authContext.Provider
      value={{
        noInternet,
        loadingImg,
        currentUser,
        signup,
        login,
        logout,
        getuser,
        uploadImage,
        validateImage,
        gcecookies,
      }}
    >
      {props.children}
    </authContext.Provider>
  );
}
